.Projects {
  background: #1c232e;
  padding: 9rem 7rem;
  padding-top: 6rem;
  display: flex;
  flex-direction: column;
}

.Projects > h1 {
  color: #fff;
  font-size: 3.6rem;
  font-family: "Work Sans", sans-serif;
  display: flex;
  justify-content: space-around;
  width: 40%;
  text-align: right;
  align-items: center;
  font-weight: 200;
  padding: 6rem 3.5rem;
  align-self: flex-end;
  letter-spacing: 1.5px;
}

@media (max-width: 767px) {
  .Projects {
    padding: 9rem 2rem;
    width: 100vw;
    padding-top: 1rem;
  }
  .Projects > h1 {
    font-size: 30px;
    padding-bottom: 2rem;
  }
}
