.Navbar {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3rem 15rem;
  top: 0;
  right: 0;
  left: 0;
  height: 15vh;
  width: 100vw;
  z-index: 3;
  /* background: transparent; */
}

.Nav-Toggler {
  display: flex;
  flex-direction: column;
  background: transparent;
  border: none;
  outline: none;
  padding: 15px;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.Nav-List {
  display: flex;
  justify-content: flex-start;
  list-style: none;
  width: 100%;
  margin: 0 40px;
  animation: slide-out 0.5s ease;
}

.Nav-List > li {
  color: #bcbfc2;
  margin: 30px;
  padding: 3px;
  letter-spacing: 2.5px;
  font-size: 0.9rem;
}

.Nav-List > li > .active {
  border-bottom: 2px solid #676cdb;
}

.Nav-Toggler > span {
  width: 25px;
  height: 3px;
  background-color: #fff;
  border-radius: 3px;
  margin: 3.5px 2px;
}

.Nav-Toggler:hover {
  background-color: #11111160;
}

@keyframes slide-out {
  from {
    opacity: 0;
    transform: translateX(-1vw);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@media (max-width: 767px) {
  .Navbar {
    justify-content: flex-start;
    padding: 10px;
  }
  .Nav-Toggler {
    background-color: #11111160;
    backdrop-filter: blur(1px);
  }
  /* .Nav-Toggler {
    position: fixed;
    top: 0;
    margin: 10px;
    left: 0;
  } */
  .Nav-Links {
    flex-direction: column;
  }
  .Nav-List {
    display: none;
  }
  .Nav-List > li {
    display: none;
  }
  .Nav-Name {
    display: none;
  }
}
