.Experience {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 5rem 0.5rem;
  padding-top: 20rem;
  background: linear-gradient(to bottom, #010001, #1c232e);
}

.Experience > h1 {
  color: #fff;
  font-size: 3.6rem;
  font-family: "Work Sans", sans-serif;
  display: flex;
  justify-content: space-around;
  width: 30%;
  text-align: right;
  align-items: center;
  font-weight: 200;
  padding: 3rem 3.5rem;
  margin-left: 10rem;
  align-self: flex-start;
  letter-spacing: 1.5px;
}

.Experience__Cards {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

@media (max-width: 767px) {
  .Experience {
    margin-top: 0;
    padding: 0.2rem 0.5rem;
    padding-top: 10rem;
    width: 100vw;
  }
  .Experience__Cards {
    flex-direction: column;
  }
}

@media (max-width: 767px) {
  .Experience {
    /* padding: 9rem 2rem; */
    width: 100vw;
    padding-top: 1rem;
  }
  .Experience > h1 {
    font-size: 30px;
    margin-left: 30px;
    padding-top: 6rem;
    padding-bottom: 1rem;
  }
}
