.Logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.Logo-Name-Light {
  font-size: 1.15rem;
  color: #ffffff;
  letter-spacing: 4px;
  font-weight: 200;
}

.Logo-Name-Slash {
  margin: 0 10px;
  background-color: #676cdb;
  margin-top: -2px;
  transform: rotateZ(20deg);
  height: 30px;
  width: 3px;
}

.Logo > svg {
  height: 20px;
  width: auto;
  align-self: center;
}

@media (max-width: 767px) {
  .Logo-Name-Light {
    font-size: calc(1.15rem * 3.1 / 4);
    letter-spacing: calc(4px * 3.1 / 4);
  }
  .Logo-Name-Slash {
    margin: 0 calc(10px * 3.1 / 4);
    margin-top: calc(-2px * 3.1 / 4);
    height: calc(30px * 3.1 / 4);
    width: calc(3px * 3.1 / 4);
  }
  .Logo > svg {
    height: calc(20px * 3.1 / 4);
  }
}
