.Card {
  height: 28rem;
  width: 20rem;
  padding: 5rem 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 2rem;
  box-shadow: 0 2px 4px 6px #00000030;
  z-index: 1;
}

.Card > h2 {
  font-size: 2rem;
  margin: 2rem 0;
  letter-spacing: 1.5px;
  font-weight: lighter;
}

.Card > p {
  font-size: 0.8rem;
  letter-spacing: 1.5px;
  line-height: 1.4rem;
}

.Card > svg {
  align-self: flex-start;
}

@media (max-width: 767px) {
  .Card {
    transform: scale(0.9);
    margin: 10px;
  }
}
