@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: all 0.8s;
  cursor: url(/static/media/cursor.bc714cca.svg), auto;
  font-family: "Nunito", sans-serif;
}

*::-webkit-scrollbar {
  width: 4px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: #828c99;
  width: 4px;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  background: #222a36;
  overflow-x: hidden;
}

button,
a {
  outline: 0;
}

.Card {
  height: 28rem;
  width: 20rem;
  padding: 5rem 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 2rem;
  box-shadow: 0 2px 4px 6px #00000030;
  z-index: 1;
}

.Card > h2 {
  font-size: 2rem;
  margin: 2rem 0;
  letter-spacing: 1.5px;
  font-weight: lighter;
}

.Card > p {
  font-size: 0.8rem;
  letter-spacing: 1.5px;
  line-height: 1.4rem;
}

.Card > svg {
  align-self: flex-start;
}

@media (max-width: 767px) {
  .Card {
    transform: scale(0.9);
    margin: 10px;
  }
}

.Experience {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 5rem 0.5rem;
  padding-top: 20rem;
  background: linear-gradient(to bottom, #010001, #1c232e);
}

.Experience > h1 {
  color: #fff;
  font-size: 3.6rem;
  font-family: "Work Sans", sans-serif;
  display: flex;
  justify-content: space-around;
  width: 30%;
  text-align: right;
  align-items: center;
  font-weight: 200;
  padding: 3rem 3.5rem;
  margin-left: 10rem;
  align-self: flex-start;
  letter-spacing: 1.5px;
}

.Experience__Cards {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

@media (max-width: 767px) {
  .Experience {
    margin-top: 0;
    padding: 0.2rem 0.5rem;
    padding-top: 10rem;
    width: 100vw;
  }
  .Experience__Cards {
    flex-direction: column;
  }
}

@media (max-width: 767px) {
  .Experience {
    /* padding: 9rem 2rem; */
    width: 100vw;
    padding-top: 1rem;
  }
  .Experience > h1 {
    font-size: 30px;
    margin-left: 30px;
    padding-top: 6rem;
    padding-bottom: 1rem;
  }
}

.Hero {
  width: 100%;
  padding: 3.5rem 0.5rem;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: url(/static/media/video.8b473a6d.mp4);
}

video {
  position: absolute;
  width: 100%;
  z-index: -1;
}

.Hero__Left {
  font-family: "Work Sans", sans-serif;
  letter-spacing: 2px;
  color: #ffffff;
  padding: 0 10px;
  font-size: 5rem;
  transform: translateY(90%);
}

.Hero__Right {
  width: 20%;
  letter-spacing: 2px;
}

.Hero__Right > h3 {
  color: #ffffff;
  font-weight: 200;
  padding: 10px 0;
}

.Hero__Right > p {
  font-size: 0.8rem;
  color: #828c99;
  line-height: 24px;
}

.Hero__Right > p:first-child {
  text-transform: uppercase;
}

.Hero__Right > ul {
  list-style: none;
  display: flex;
  position: absolute;
  flex-direction: row;
  margin-top: 10px;
}

.Hero__Right > ul > li {
  margin: 10px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Hero__Right > ul > li > a > svg {
  color: #ffffff;
  transform: scale(1.5);
}

@media (max-width: 767px) {
  .Hero {
    height: 100vh;
    padding: 0 2.5rem;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
  }
  .Hero__Left {
    display: none;
  }
  .Hero__Right {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
  }
  .Hero__Right > h3 {
    font-size: 18px;
  }
  .Hero__Right > p {
    font-size: 12px;
  }
  .Hero__Right > ul > li > a > svg {
    transform: scale(0) !important;
  }
  video {
    height: auto;
    width: 100vh;
    transform: rotateZ(90deg);
  }
}

.Contacts {
  list-style: none;
  display: flex;
  flex-direction: row;
  margin-top: 10px;
}

.Contacts > li {
  margin: 10px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Contacts > li > a > svg {
  color: #ffffff;
  transform: scale(1.5);
}
.Navbar {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3rem 15rem;
  top: 0;
  right: 0;
  left: 0;
  height: 15vh;
  width: 100vw;
  z-index: 3;
  /* background: transparent; */
}

.Nav-Toggler {
  display: flex;
  flex-direction: column;
  background: transparent;
  border: none;
  outline: none;
  padding: 15px;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.Nav-List {
  display: flex;
  justify-content: flex-start;
  list-style: none;
  width: 100%;
  margin: 0 40px;
  animation: slide-out 0.5s ease;
}

.Nav-List > li {
  color: #bcbfc2;
  margin: 30px;
  padding: 3px;
  letter-spacing: 2.5px;
  font-size: 0.9rem;
}

.Nav-List > li > .active {
  border-bottom: 2px solid #676cdb;
}

.Nav-Toggler > span {
  width: 25px;
  height: 3px;
  background-color: #fff;
  border-radius: 3px;
  margin: 3.5px 2px;
}

.Nav-Toggler:hover {
  background-color: #11111160;
}

@keyframes slide-out {
  from {
    opacity: 0;
    transform: translateX(-1vw);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@media (max-width: 767px) {
  .Navbar {
    justify-content: flex-start;
    padding: 10px;
  }
  .Nav-Toggler {
    background-color: #11111160;
    -webkit-backdrop-filter: blur(1px);
            backdrop-filter: blur(1px);
  }
  /* .Nav-Toggler {
    position: fixed;
    top: 0;
    margin: 10px;
    left: 0;
  } */
  .Nav-Links {
    flex-direction: column;
  }
  .Nav-List {
    display: none;
  }
  .Nav-List > li {
    display: none;
  }
  .Nav-Name {
    display: none;
  }
}

.Logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.Logo-Name-Light {
  font-size: 1.15rem;
  color: #ffffff;
  letter-spacing: 4px;
  font-weight: 200;
}

.Logo-Name-Slash {
  margin: 0 10px;
  background-color: #676cdb;
  margin-top: -2px;
  transform: rotateZ(20deg);
  height: 30px;
  width: 3px;
}

.Logo > svg {
  height: 20px;
  width: auto;
  align-self: center;
}

@media (max-width: 767px) {
  .Logo-Name-Light {
    font-size: calc(1.15rem * 3.1 / 4);
    letter-spacing: calc(4px * 3.1 / 4);
  }
  .Logo-Name-Slash {
    margin: 0 calc(10px * 3.1 / 4);
    margin-top: calc(-2px * 3.1 / 4);
    height: calc(30px * 3.1 / 4);
    width: calc(3px * 3.1 / 4);
  }
  .Logo > svg {
    height: calc(20px * 3.1 / 4);
  }
}

.Loader {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.LoaderText {
  color: #fff;
  font-family: "Nunito", sans-serif;
  letter-spacing: 1px;
  font-size: 3rem;
  position: absolute;
  top: 20%;
}

.LoaderBalls {
  width: 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transform: scale(1.5);
}
.LoaderBalls__item {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #827f70;
}
.LoaderBalls__item:nth-child(1) {
  animation: bouncing 0.4s alternate infinite
    cubic-bezier(0.6, 0.05, 0.15, 0.95);
}
.LoaderBalls__item:nth-child(2) {
  animation: bouncing 0.4s 0.1s alternate infinite
    cubic-bezier(0.6, 0.05, 0.15, 0.95) backwards;
}
.LoaderBalls__item:nth-child(3) {
  animation: bouncing 0.4s 0.2s alternate infinite
    cubic-bezier(0.6, 0.05, 0.15, 0.95) backwards;
}

@keyframes bouncing {
  0% {
    transform: translate3d(0, 10px, 0) scale(1.2, 0.85);
  }
  100% {
    transform: translate3d(0, -20px, 0) scale(0.9, 1.1);
  }
}

.Footer {
  background-color: #222a36;
  padding: 3rem;
  padding-bottom: 0;
  width: 100%;
}

.Footer__Top {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1rem 0;
}

.Footer__Top > .Logo {
  transform: scale(1.4);
}

.Footer__Top > .ContactList {
  margin: 2rem 0;
}

.Footer__Bottom {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 2rem 0;
}

.Footer__Bottom > p {
  display: flex;
  font-size: 0.65rem;
  letter-spacing: 3px;
  font-weight: 200;
  color: #ffffff;
  justify-content: center;
  align-items: center;
}

.Footer__Bottom > p > svg {
  transform: scale(0.7);
  height: 20px;
  width: auto;
  margin: 0 2px;
}

.Footer__Scroller {
  display: flex;
  transform: translate(90vw, -12vw);
  border: 0;
  justify-content: center;
  align-self: flex-end;
  align-items: center;
  background-color: #676cdb;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
}

.ProjectCard {
  display: flex;
  padding: 3.5rem 5rem;
  background: #29313d;
  width: 100%;
  color: #ffffff;
  text-decoration: none;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
  transition: 0.5s ease-in-out;
  box-shadow: 0 2px 4px 6px #00000030;
}

.ProjectCard:hover {
  transform: scale(1.02);
}

.ProjectCard > img {
  width: 15%;
}

.ProjectCard__Text {
  display: flex;
  flex-direction: column;
  letter-spacing: 2px;
  width: 20%;
}

.ProjectCard__Text > h1 {
  font-size: 1.5rem;
  font-weight: 200;
  line-height: 4rem;
}

.ProjectCard__Text > p {
  font-size: 0.8rem;
  color: #828c99;
}

.ProjectCard__Text > div {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 3rem;
}

.ProjectCard__Text > div > svg {
  transform: scale(2);
}

@media (max-width: 767px) {
  .ProjectCard {
    /* padding: 3.5rem; */
    /* flex-direction: column; */
  }
  .ProjectCard__Text {
    width: 100%;
  }
  .ProjectCard__Text > h1 {
    line-height: 30px;
    margin: 20px 0;
  }
}

.Projects {
  background: #1c232e;
  padding: 9rem 7rem;
  padding-top: 6rem;
  display: flex;
  flex-direction: column;
}

.Projects > h1 {
  color: #fff;
  font-size: 3.6rem;
  font-family: "Work Sans", sans-serif;
  display: flex;
  justify-content: space-around;
  width: 40%;
  text-align: right;
  align-items: center;
  font-weight: 200;
  padding: 6rem 3.5rem;
  align-self: flex-end;
  letter-spacing: 1.5px;
}

@media (max-width: 767px) {
  .Projects {
    padding: 9rem 2rem;
    width: 100vw;
    padding-top: 1rem;
  }
  .Projects > h1 {
    font-size: 30px;
    padding-bottom: 2rem;
  }
}

