.ProjectCard {
  display: flex;
  padding: 3.5rem 5rem;
  background: #29313d;
  width: 100%;
  color: #ffffff;
  text-decoration: none;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
  transition: 0.5s ease-in-out;
  box-shadow: 0 2px 4px 6px #00000030;
}

.ProjectCard:hover {
  transform: scale(1.02);
}

.ProjectCard > img {
  width: 15%;
}

.ProjectCard__Text {
  display: flex;
  flex-direction: column;
  letter-spacing: 2px;
  width: 20%;
}

.ProjectCard__Text > h1 {
  font-size: 1.5rem;
  font-weight: 200;
  line-height: 4rem;
}

.ProjectCard__Text > p {
  font-size: 0.8rem;
  color: #828c99;
}

.ProjectCard__Text > div {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 3rem;
}

.ProjectCard__Text > div > svg {
  transform: scale(2);
}

@media (max-width: 767px) {
  .ProjectCard {
    /* padding: 3.5rem; */
    /* flex-direction: column; */
  }
  .ProjectCard__Text {
    width: 100%;
  }
  .ProjectCard__Text > h1 {
    line-height: 30px;
    margin: 20px 0;
  }
}
