.Footer {
  background-color: #222a36;
  padding: 3rem;
  padding-bottom: 0;
  width: 100%;
}

.Footer__Top {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1rem 0;
}

.Footer__Top > .Logo {
  transform: scale(1.4);
}

.Footer__Top > .ContactList {
  margin: 2rem 0;
}

.Footer__Bottom {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 2rem 0;
}

.Footer__Bottom > p {
  display: flex;
  font-size: 0.65rem;
  letter-spacing: 3px;
  font-weight: 200;
  color: #ffffff;
  justify-content: center;
  align-items: center;
}

.Footer__Bottom > p > svg {
  transform: scale(0.7);
  height: 20px;
  width: auto;
  margin: 0 2px;
}

.Footer__Scroller {
  display: flex;
  transform: translate(90vw, -12vw);
  border: 0;
  justify-content: center;
  align-self: flex-end;
  align-items: center;
  background-color: #676cdb;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
}
