.Hero {
  width: 100%;
  padding: 3.5rem 0.5rem;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: url("../../assets/videos/video.mp4");
}

video {
  position: absolute;
  width: 100%;
  z-index: -1;
}

.Hero__Left {
  font-family: "Work Sans", sans-serif;
  letter-spacing: 2px;
  color: #ffffff;
  padding: 0 10px;
  font-size: 5rem;
  transform: translateY(90%);
}

.Hero__Right {
  width: 20%;
  letter-spacing: 2px;
}

.Hero__Right > h3 {
  color: #ffffff;
  font-weight: 200;
  padding: 10px 0;
}

.Hero__Right > p {
  font-size: 0.8rem;
  color: #828c99;
  line-height: 24px;
}

.Hero__Right > p:first-child {
  text-transform: uppercase;
}

.Hero__Right > ul {
  list-style: none;
  display: flex;
  position: absolute;
  flex-direction: row;
  margin-top: 10px;
}

.Hero__Right > ul > li {
  margin: 10px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Hero__Right > ul > li > a > svg {
  color: #ffffff;
  transform: scale(1.5);
}

@media (max-width: 767px) {
  .Hero {
    height: 100vh;
    padding: 0 2.5rem;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
  }
  .Hero__Left {
    display: none;
  }
  .Hero__Right {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
  }
  .Hero__Right > h3 {
    font-size: 18px;
  }
  .Hero__Right > p {
    font-size: 12px;
  }
  .Hero__Right > ul > li > a > svg {
    transform: scale(0) !important;
  }
  video {
    height: auto;
    width: 100vh;
    transform: rotateZ(90deg);
  }
}
